<template>
  <div id="order-detail" class="order-detail-container">
    <el-row>
      <el-col>
        <el-card class="card-box line-height-30" shadow="hover">
          <el-row>
            <el-col><h4>订单基本信息：</h4></el-col>
          </el-row>
          <el-row>
            <el-col :span="8" :xs="24">订单编号：{{order.number}}</el-col>
            <el-col :span="8" :xs="24">服务类型：{{order.serviceType}}</el-col>
            <el-col :span="8" :xs="24">下单时间：{{order.orderTime}}</el-col>
          </el-row>
          <el-row>
            <el-col :span="8" :xs="24">雇主ID：{{order.employerId}}</el-col>
            <el-col :span="8" :xs="24">雇主姓名：{{order.employerName}}</el-col>
            <el-col :span="8" :xs="24">手机号码：{{order.employerPhone}}</el-col>
          </el-row>
          <el-row>
            <el-col :span="8" :xs="24">雇主地址：{{order.employerAddr}}</el-col>
            <el-col :span="8" :xs="24">支付状态：{{order.payStatus}}</el-col>
            <el-col :span="8" :xs="24">服务状态：{{order.serviceStatus}}</el-col>
          </el-row>
          <hr>
          <el-row>
            <el-col><h4>订单服务信息：</h4></el-col>
          </el-row>
          <el-row>
            <el-col :span="8" :xs="24">订单总金额：{{order.orderAmount}}元</el-col>
            <el-col :span="8" :xs="24">财产保险费：暂无</el-col>
            <el-col :span="8" :xs="24">住家与否：{{order.asideIn}}</el-col>
          </el-row>
          <el-row>
            <el-col :span="8" :xs="24">订单时长：{{order.orderDuration}}</el-col>
            <el-col :span="8" :xs="24">服务开始时间：{{order.serviceStartTime}}</el-col>
            <el-col :span="8" :xs="24">服务截至时间：{{order.serviceEndTime}}</el-col>
          </el-row>
          <el-row>
            <el-col :span="24" :xs="24">其他备注：{{order.backup || '无'}}</el-col>
          </el-row>
          <hr class="mb-20">
          <el-row>
            <el-col>
              <el-tabs v-model="activeTab" @tab-click="changeTab">
                <el-tab-pane :label="item.label" :name="item.name" :key="item.id"
                  v-for="item in orderDetailTabs"></el-tab-pane>
              </el-tabs>
            </el-col>
          </el-row>
          <el-row>
            <el-col>
              <order-detail-table  :tableData="records" :isPagination="isPagination" :fields="fields"
                @gotoAyiDetail="gotoAyiDetail" @checkEcontract="checkEcontract"></order-detail-table>
            </el-col>
          </el-row>
          <el-row>
            <el-col><center><el-button type="primary" size="small" @click="close">关 闭</el-button></center></el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
/**
 * @author 吴永军
 * @description 服务订单详情
 *  */
import orderDetailTable from './OrderTable'
import {
  serOrderDetail,
  tabToFields,
  tabToGetRecord,
  tabToTransData
} from './orderTable.config'
import { transServiceOrderDetail, makeStatusMap } from './orderForm.config'
import { getSerOrderDetail, getSerOrdersConditions } from '../../../service/manageOrdersService'
export default {
  name: 'orderDetail',
  data () {
    return {
      order: {
        id: -1
      },
      activeTab: 'ayiInfos',
      orderDetailTabs: serOrderDetail,
      isPagination: false,
      records: [],
      fields: [],
      tabToFields
    }
  },
  components: {
    'order-detail-table': orderDetailTable
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['订单管理', '服务订单', '订单详情'])
    this.order.id = this.$route.params.orderId
    this.fields = tabToFields[this.activeTab]
    // 获取订单详情
    this.fetchConditions()
  },
  methods: {
    fetchConditions () {
      getSerOrdersConditions().then(res => {
        this.$store.dispatch('commitChangeOrderStatus', makeStatusMap(res.data))
        this.fetchOrderDetail()
        // 获取阿姨记录
        this.fetchRecords('ayiInfos')
      })
    },
    fetchOrderDetail () {
      getSerOrderDetail(this.order.id).then(res => {
        let orderStatus = this.$store.getters.getOrderStatus
        this.order = transServiceOrderDetail(res.data, orderStatus)
      })
    },
    changeTab (tab, event) {
      this.fields = tabToFields[tab.name]
      this.fetchRecords(tab.name)
    },
    fetchRecords (tab) {
      tabToGetRecord[tab](this.order.id).then(res => {
        let orderStatus = this.$store.getters.getOrderStatus
        this.records = tabToTransData[tab](res.data.items || res.data, orderStatus)
      })
    },
    gotoAyiDetail (order) {
      this.$router.push(`/AuntDetail/${order.ayiId}`)
    },
    checkEcontract (order) {
      // this.$router.push({
      //   name: '查看电子合同',
      //   query: { path: order.contract }
      // })
      window.open(order.contract, '_blank')
    },
    close () {
      this.$router.push(`/serviceOrders`)
    }
  }
}
</script>
<style scoped>
@import "../../../assets/styles/common.css";
h4 {
  margin: 5px 0px;
}
.line-height-30 {
  line-height: 30px;
}
.mb-20 {
  margin-bottom: 20px;
}
hr {
  height:1px;
  border:none;
  border-top:1px dashed rgb(210, 208, 208);
  margin: 12px 0px;
}
</style>



